.modal-wrap {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgba(51, 51, 51, 0.7);
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  @media (max-width: 1200px) {
    // overflow: auto;
    display: block;
    // position: absolute;
    // bottom: auto;
    // width: 100%;
    // height: 100%;
  }
  &.active {
    opacity: 1;
    visibility: visible;
  }
}
.modal-close {
  width: 30px;
  height: 30px;
  background-color: #fd5f5f;
  display: block;
  border-radius: 50%;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  transition: all 0.2s ease;
  &:before {
    content: '';
    width: 20px;
    height: 2px;
    background-color: #fff;
    display: block;
    position: absolute;
    top: 14px;
    left: 5px;
    transform: rotate(48deg);
  }
  &:after {
    content: '';
    width: 20px;
    height: 2px;
    background-color: #fff;
    display: block;
    position: absolute;
    top: 14px;
    left: 5px;
    transform: rotate(130deg);
  }
  &:hover {
    background-color: darken(#fd5f5f, 5%);
  }
}

.modal {
  width: 1200px;
  height: 855px;
  background-color: #ffffff;
  display: none;
  @media(max-width: 1200px) {
    width: 100%;
    height: 100%;
    overflow: auto;
  }
  &.active {
    display: block;
  }
  &_head {
    height: 119px;
    background-color: #808289;
    position: relative;
    @media (max-width: 768px) {
      min-height: 119px;
      height: auto;
    }
  }
  &_body {
    padding: 54px 62px 50px 62px;
    height: 625px;
    @media(max-width: 1200px) {
      height: auto;
    }
    @media (max-width: 768px) {
      padding: 50px 10px;
    }
  }
  &_bottom {
    padding: 10px 62px 39px 62px;
    display: flex;
    align-items: center;
    @media (max-width: 768px) {
      display: block;
      padding: 10px 10px 35px 10px;
    }
  }
  &_btn {
    width: 29%;
    min-width: 29%;
    height: 80px;
    box-shadow: 0 5px 20px 1px rgba(235, 235, 235, 0.85);
    border-radius: 8px;
    border: 1px solid #636363;
    margin-right: 90px;
    font-size: 1.125rem;
    cursor: pointer;
    background-color: #ffffff;
    transition: all 0.2s ease;
    @media (max-width: 768px) {
      width: 260px;
      margin-bottom: 30px;
    }
    @media (max-width: 480px) {
      margin: 0 auto 30px auto;
      display: block;
    }
    &:hover {
      background-color: #f3f3f3;
    }
  }
  &_description {
    color: #28282d;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
  }
}

.modal-nav {
  font-family: 'Century', sans-serif;
  padding: 36px 62px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  @media (max-width: 768px) {
    display: block;
    padding: 20px 10px;
  }
  &_item {
    font-size: 2.25rem;
    color: #ffffff;
    text-decoration: none;
    margin-right: 38px;
    cursor: pointer;
    @media (max-width: 1200px) {
      font-size: 1.375rem;
      margin-right: 20px;
    }
    @media (max-width: 600px) {
      padding-left: 20px;
      font-size: 1.125rem;
    }
    &:after {
      content: '';
      width: 1px;
      height: 28px;
      background-color: #ffffff;
      display: inline-block;
      margin-left: 38px;
      @media (max-width: 1200px) {
        height: 20px;
        margin-left: 20px;
      }
      @media (max-width: 768px) {
        display: none;
      }
    }
    &:hover {
      color: #fd5f5f;
    }
    &:last-child {
      pointer-events: none;
      &:hover {
        color: #fff;
      }
      &:after {
        display: none;
      }
    }
    &:first-child:hover {
      color: #ffffff;
      cursor: default;
    }
  }
  &_title {
    font-size: 2.25rem;
    text-transform: uppercase;
    @media (max-width: 1200px) {
      font-size: 1.75rem;
    }
    @media (max-width: 768px) {
      margin-bottom: 5px;
    }
    @media (max-width: 600px) {
      padding-left: 0px;
    }
  }
}

.models {
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 768px) {
    justify-content: space-between;
  }
  &_item {
    width: 32%;
    height: 254px;
    box-shadow: 0 5px 20px 1px rgba(235, 235, 235, 0.85);
    border-radius: 8px;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px 60px;
    margin-right: 1.33%;
    margin-bottom: 14px;
    position: relative;
    cursor: pointer;
    transition: all 0.1s ease;
    @media(max-width: 1200px) {
      width: 32.3%;
      height: auto;
      min-height: 254px;
    }
    @media (max-width: 1100px) {
      padding: 20px;
    }
    @media (max-width: 768px) {
      width: 49%;
      margin-right: 0;
    }
    @media (max-width: 480px) {
      width: 100%;
      margin-bottom: 8px;
      min-height: 0;
    }
    &:hover {
      box-shadow: 0 5px 20px 1px rgba(235, 235, 235, 0.3);
    }
    &:nth-child(3n) {
      margin-right: 0;
    }
  }
  &_icon {
    margin-bottom: 19px;
  }
  &_title {
    color: #1e1e1e;
    font-size: 1.188rem;
    font-weight: 400;
    margin-bottom: 17px;
    text-align: center;
    @media (max-width: 480px) {
      margin-bottom: 10px;
    }
  }
  &_cost {
    font-size: 1.5rem;
    color: #232530;
    ont-family: 'Century', sans-serif;
  }
}

.c-active {
  &:after {
    content: '';
    width: 29px;
    height: 29px;
    background-color: #fd5f5f;
    display: block;
    position: absolute;
    border-radius: 50%;
    top: 20px;
    right: 20px;
    background-image: url(../images/check.png);
    background-repeat: no-repeat;
    background-position: center;
  }
}

.cut {
  display: flex;
  flex-wrap: wrap;
  @media (max-width: 900px) {
    justify-content: space-between;
  }
  &_item {
    background-color: #f3f3f3;
    width: 226px;
    height: 260px;
    padding: 25px 35px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin-right: 1.17%;
    margin-bottom: 14px;
    position: relative;
    @media (max-width: 1200px) {
      width: 32%;
    }
    @media (max-width: 900px) {
      width: 49%;
      height: auto;
      margin-right: 0;
    }
    &:nth-child(3n) {
      margin-right: 0;
    }
  }
  &_icon {
    margin-bottom: 21px;
  }
  &_title {
    color: #1e1e1e;
    font-size: 1.125rem;
  }
}

.tab {
  display: flex;
  @media (max-width: 768px) {
    display: block;
  }
}
.tab-head {
  margin-right: 1.67%;
  @media (max-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
    margin-right: 0;
  }
  @media (max-width: 480px) {
    justify-content: space-between;
  }
  &_item {
    width: 348px;
    height: 80px;
    box-shadow: 0 5px 20px 1px rgba(235, 235, 235, 0.85);
    border-radius: 8px;
    background-color: #ffffff;
    color: #1e1e1e;
    font-size: 1.125rem;
    margin-bottom: 11px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 38px;
    cursor: pointer;
    @media (max-width: 1200px) {
      width: 280px;
    }
    @media (max-width: 1024px) {
      width: 240px;
      padding: 15px 20px;
      height: auto;
    }
    @media (max-width: 768px) {
      width: auto;
      margin-right: 11px;
    }
    @media (max-width: 480px) {
      width: 49%;
      margin-right: 0;
    }
    &:after {
      content: '';
      width: 6px;
      height: 10px;
      background-image: url(../images/sprite.svg#next);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      @media (max-width: 768px) {
        display: none;
      }
    }
    &:hover {
      box-shadow: 0 5px 20px 1px rgba(235, 235, 235, 0.5);
    }
    &.active {
      box-shadow: 0 5px 20px 1px rgba(235, 235, 235, 0.85);
      background-color: #fd5f5f;
      color: #ffffff;
      &:after {
        background-image: url(../images/sprite.svg#next-white);
      }
    }
  }
}
.tab-body {
  width: 100%;
  &_item {
    display: none;
    &:first-child {
      display: block;
    }
    .cloth-right__item {
      position: relative;
    }
  }
}

.c-cloth {
  display: flex;
  flex-wrap: wrap;
  height: 550px;
  overflow-y: auto;
  scrollbar-color: #808289 #ebebeb;
  &::-webkit-scrollbar { width: 5px;}
  &::-webkit-scrollbar-button { display: none;}
  &::-webkit-scrollbar-track {  background-color: #ebebeb;}
  &::-webkit-scrollbar-track-piece { background-color: #ebebeb;}
  &::-webkit-scrollbar-thumb { background-color: #808289;}
  &::-webkit-scrollbar-corner { background-color: #ebebeb;}
  @media (max-width: 1024px) {
    justify-content: space-between;
    height: auto;
  }
  &_item {
    width: 225px;
    margin-right: 0.83%;
    margin-bottom: 20px;
    text-align: center;
    cursor: pointer;
    position: relative;
    @media (max-width: 1200px) {
      width: 32%;
    }
    @media (max-width: 1024px) {
      width: 49%;
      margin-right: 0;
    }
    &:nth-child(3n) {
      margin-right: 0;
    }
  }
  &_img {
    margin-bottom: 18px;
    height: 215px;
    width: 100%;
    object-fit: cover;
    @media (max-width: 480px) {
      height: 140px;
      margin-bottom: 10px;
    }
  }
  &_title {
    color: #1e1e1e;
    font-size: 1.125rem;
  }
}

.result {
  background-color: #f3f3f3;
  width: 56.42%;
  height: 176px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto 25px auto;
  @media (max-width: 768px) {
    width: 100%;
  }
  &_title {
    font-size: 1.5rem;
    color: #1e1e1e;
    margin-bottom: 12px;
    display: block;
  }
  &_cost {
    font-size: 3rem;
    color: #232530;
    font-family: 'Century', sans-serif;
  }
}


.m-form {
  max-width: 55.92%;
  width: 55.92%;
  margin: 0 auto;
  @media (max-width: 768px) {
    max-width: 80%;
    width: 80%;
  }
  @media (max-width: 480px) {
    max-width: 100%;
    width: 100%;
  }
  &_wrap {
    margin: 0 auto;
    text-align: center;
  }
  &_title {
    font-family: 'Century', sans-serif;
    font-size: 2.25rem;
    color: #232530;
    margin-bottom: 20px;
  }
  input {
    margin-bottom: 16px;
    text-align: center;
  }
  &_btn {
    margin: 0 auto;
    box-shadow: 0 5px 20px 1px rgba(235, 235, 235, 0.85);
    width: 347px;
    height: 69px;
    border-radius: 8px;
    background-color: #fd5f5f;
    color: #ffffff;
    font-size: 1.125rem;
    text-transform: uppercase;
    cursor: pointer;
    transition: all 0.2s ease;
    &:hover {
      background-color: darken(#fd5f5f, 5%);
    }
  }
}
