.footer {
  background-color: #1e1f26;
  color: #fff;
  &__wrap {

  }
  &__top {
    height: 133px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $border;
  }
  &__bottom {
    font-size: 12px;
    font-weight: 300;
    color: #c3c3c3;
    text-align: center;
    padding: 20px 100px;
    @media (max-width: 768px) {
      padding: 20px;
    }
    @media (max-width: 480px) {
      padding: 20px 0px;
    }
  }
  &__column {
    display: flex;
    align-items: center;
    @media (max-width: 768px) {
      flex-direction: column;
      align-items: flex-end;
    }
  }
}


.f-logo {
  margin-right: 120px;
  @media (max-width: 1024px) {
    margin-right: 20px;
  }
}

.f-contacts {
  font-size: 14px;
  font-weight: 300;
  @media (max-width: 960px){
    display: none;
  }
  span {
    margin-right: 70px;
    @media (max-width: 1024px) {
      margin-right: 30px;
    }
  }
  a {
    color: #fff;
    text-decoration: none;
  }
}

.social {
  margin-right: 50px;
  &__item {
    &.inst {
      width: 21px;
      height: 21px;
      background-image: url(../images/sprite.svg#inst);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      display: block;
    }
  }
  @media (max-width: 768px) {
    margin-right: 0;
    margin-bottom: 10px;
  }
}


.dev {
  a {
    color: $accent;
    &:hover {
      text-decoration: none;
    }
  }
}
