.school-main {
  background-image: url(../images/school_bg.png);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: auto 100%;
  padding-bottom: 10px;
  @media (max-width: 768px) {
    background: none;
  }
  &__text-wrap {
    padding: 60px 30px;
    max-width: 625px;
    @media (max-width: 1024px) {
      background-color: rgba(245, 245, 245, 0.9);
    }
    @media (max-width: 768px) {
      padding: 50px 15px;
    }
    @media (max-width: 480px) {
      padding: 30px 15px;
    }
  }
  &__text {
    margin-bottom: 34px;
    color: #6c6e75;
  }
  h1 {
    color: #333439;
  }
}

.inner-title-block {
  background-color: #f5f5f4;
  padding-bottom: 30px;
  @media (max-width: 480px) {
    margin-bottom: 20px;
  }
  .breadcrumb {
    margin-bottom: 30px;
    @media (max-width: 480px) {
      margin-bottom: 20px;
    }
  }
  h1 {
    text-align: center;
  }
  &.left h1 {
    text-align: left;
    padding-left: 50px;
    @media (max-width: 768px) {
      padding-left: 10px;
    }
  }
}

.catalog {
  .assortment__list {
		display: grid;
    grid-template-rows: repeat(5, 330px);
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 1vw;
		grid-template-areas:
    'a3 a3 a2'
    'a3 a3 a6'
    'a1 a1 a8'
    'a4 a5 a9'
    'a7 a5 a10';
		@media (max-width: 1024px) {
			grid-template-rows: repeat(4, 310px);
			grid-template-columns: 1fr 1fr 1fr;
      grid-template-areas:
			'a3 a3 a3'
			'a1 a2 a4'
			'a5 a6 a7'
			'a8 a9 a10';
		}
		@media (max-width: 768px) {
			grid-template-rows: repeat(6, 310px);
			grid-template-columns: 1fr 1fr;
      grid-template-areas:
			'a3 a3'
			'a1 a2'
			'a4 a4'
			'a5 a6'
			'a7 a8'
			'a9 a10';
		}
		@media (max-width: 480px) {
			grid-template-rows: repeat(10, 260px);
			grid-template-columns: 1fr;
      grid-template-areas:
			'a1'
			'a2'
			'a3'
			'a4'
			'a5'
			'a6'
			'a7'
			'a8'
			'a9'
			'a10';
		}
	}
}

.price {
  &__list {
    padding: 60px 30px;
    display: flex;
    flex-wrap: wrap;
    @media (max-width: 1024px) {
      padding: 60px 0;
    }
    @media (max-width: 480px) {
      padding: 30px 0;
    }
  }
  &__item {
    border-right: 1px solid #cccccc;
    border-bottom: 1px solid #cccccc;
    width: 25%;
    text-align: center;
    padding: 135px 31px 60px 31px;
    position: relative;
    &:nth-last-child(-n+3) {
      border-bottom: 0;
    }
    @media (max-width: 1024px) {
      width: 33%;
    }
    @media (max-width: 768px) {
      width: 50%;
      &:nth-last-child(-n+3) {
        border-bottom: 1px solid #cccccc;
      }
    }
    @media (max-width: 480px) {
      width: 100%;
      border-right: 0;
      padding: 122px 31px 30px 31px;
    }
  }
  &__icon {
    margin-bottom: 21px;
    margin-left: auto;
    margin-right: auto;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: absolute;
    top: 40px;
    left: 50%;
    @media (max-width: 480px) {
      top: 28px;
    }
    &.suit-2 {
      width: 99px;
      height: 68px;
      margin-left: -50px;
      background-image: url(../images/sprite.svg#suit-2);
    }
    &.suit-3 {
      width: 145px;
      height: 68px;
      margin-left: -72px;
      background-image: url(../images/sprite.svg#suit-3);
    }
    &.jacket {
      width: 62px;
      height: 70px;
      margin-left: -31px;
      background-image: url(../images/sprite.svg#jacket);
    }
    &.trousers {
      width: 36px;
      height: 71px;
      margin-left: -18px;
      background-image: url(../images/sprite.svg#trousers);
    }
    &.vest {
      width: 42px;
      height: 71px;
      margin-left: -21px;
      background-image: url(../images/sprite.svg#vest);
    }
    &.shirt {
      width: 59px;
      height: 75px;
      margin-left: -30px;
      background-image: url(../images/sprite.svg#shirt);
    }
    &.coat {
      width: 54px;
      height: 85px;
      margin-left: -27px;
      background-image: url(../images/sprite.svg#coat);
    }
  }
  &__title {
    font-size: 1.125rem;
    font-weight: 400;
    margin-bottom: 18px;
    min-height: 44px;
    @media (max-width: 480px) {
      margin-bottom: 8px;
      min-height: 0px;
    }
  }
  &__cost {
    font-family: 'Century', sans-serif;
    font-size: 1.5rem;
  }
}

.insert {
  text-align: center;
  &__title {
    font-size: 1.125rem;
    font-weight: 400;
    color: #161616;
    display: block;
  }
  &__subtitle {
    color: #6c6e75;
    font-size: 0.875rem;
    display: block;
    margin-bottom: 10px;
  }
}


.inner-head {
  background-color: #f5f5f4;
  height: 480px;
	background-repeat: no-repeat;
	background-position: right bottom;
  &--png {
    background-position: right 100px bottom;
  }
	@media (max-width: 1100px) {
		background-image: none!important;
    height: auto;
	}
  &__title {
    color: #333439;
    font-family: 'Century', sans-serif;
    font-size: 2.25rem;
    font-weight: 400;
    text-transform: uppercase;
    padding: 120px 30px 30px 105px;
    max-width: 616px;
    @media (max-width: 1100px) {
      padding: 40px 30px 60px 60px;
    }
    @media (max-width: 768px) {
      padding-left: 15px;
    }
    @media (max-width: 480px) {
      padding-left: 0px;
    }
  }
}


.about-block {
	&__wrap {
		display: flex;
		@media (max-width: 768px) {
			display: block;
		}
	}
	&__left {
		padding: 48px 40px 30px 30px;
    width: 60%;
    @media (max-width: 768px) {
      width: 100%;
      padding: 0px 0px;
    }
	}
	&__right {
    width: 40%;
		padding: 50px 0px 30px 20px;
    @media (max-width: 768px) {
      width: 100%;
      padding: 0px 0px;
      img {
        margin: 0 auto;
      }
    }
	}
}


.nonstandart {
  display: flex;
  @media (max-width: 960px) {
    display: block;
  }
  &__img {
    width: 548px;
    min-width: 548px;
    @media (max-width: 1200px) {
      width: 420px;
      min-width: 420px;
    }
    @media (max-width: 960px) {
      display: none;
    }
  }
  &__text-wrap {
    background-color: #f5f5f4;
    padding: 89px 70px;
    color: #6c6e75;
    font-size: 1.125rem;
    line-height: 1.625rem;
    @media (max-width: 480px) {
      padding: 40px 30px;
    }
  }
  &__title {
    color: #333439;
    font-family: 'Century', sans-serif;
    font-size: 2.25rem;
    text-transform: uppercase;
    line-height: normal;
  }
}


.stages {
  &__item {
    display: flex;
    margin-bottom: 100px;
    @media (max-width: 768px) {
      margin-bottom: 60px;
    }
    @media (max-width: 560px) {
      flex-direction: column;
    }
  }
  &__img {
    width: 388px;
    min-width: 388px;
    @media (max-width: 768px) {
      width: 280px;
      min-width: 280px;
    }
    @media (max-width: 560px) {
      width: 100%;
      min-width: 0;
      order: -1;
      height: 260px;
      object-fit: cover;
    }
  }
  &__text-wrap {
    padding: 48px 80px;
    @media (max-width: 920px) {
      padding: 20px 30px;
    }
  }
  &__number {
    font-family: Century;
    font-size: 100px;
    line-height: 83px;
    text-transform: uppercase;
    color: #2b282f;
    margin-bottom: 55px;
    display: block;
    @media (max-width: 920px) {
      font-size: 80px;
      line-height: 60px;
      margin-bottom: 30px;
    }
    @media (max-width: 768px) {
      font-size: 60px;
      line-height: 50px;
      margin-bottom: 20px;
    }
    @media (max-width: 480px) {
      font-size: 50px;
      line-height: 40px;
    }
  }
  &__title {
    font-family: 'Century', sans-serif;
    color: #232530;
    font-size: 1.5rem;
    font-weight: 400;
    margin-bottom: 50px;
    width: 80%;
    @media (max-width: 920px) {
      width: 100%;
      margin-bottom: 30px;
    }
    @media (max-width: 768px) {
      margin-bottom: 20px;
    }
  }
  &__text {
    color: #6c6e75;
    font-size: 1.125rem;
    line-height: 1.625rem;
  }
}

.inset {
  padding: 122px 60px;
  color: #6c6e75;
  font-size: 1.125rem;
  line-height: 24px;
  @media (max-width: 480px) {
    padding: 40px 20px;
  }
}


.cost {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 50px 0px;
  &__item {
    width: 49%;
    background-color: #ffffff;
    box-shadow: 0 5px 44px 2px rgba(83, 83, 83, 0.21);
    text-align: center;
    padding: 32px 30px 40px 30px;
    @media (max-width: 900px) {
      width: 100%;
      margin-bottom: 20px;
    }
    @media (max-width: 480px) {
      padding: 26px 10px 30px 10px;
    }
  }
  &__title {
    font-family: 'Century', sans-serif;
    font-size: 1.688rem;
    font-weight: 400;
    margin-bottom: 60px;
    @media (max-width: 480px) {
      margin-bottom: 20px;
    }
  }
  &__subtitle {
    color: #6c6e75;
    font-size: 0.875rem;
    display: block;
    margin-bottom: 6px;
  }
  &__description {
    font-size: 1.125rem;
    color: #161616;
  }
  &__wrap {
    margin-bottom: 54px;
    display: flex;
    @media (max-width: 480px) {
      display: block;
      margin-bottom: 20px;
    }
  }
}


.cost-column {
  position: relative;
  border-right: 1px solid #cccccc;
  width: 50%;
  padding: 100px 10px 0 10px;
  @media (max-width: 480px) {
    width: 90%;
    margin: 0 auto;
    border-right: none;
    border-bottom: 1px solid #cccccc;
    padding-bottom: 20px;
  }
  &:last-child {
    border: none;
  }
  .price__icon {
    top: 0;
    @media (max-width: 480px) {
      top: 20px;
    }
  }
  &__title {
    font-size: 1.25rem;
    font-weight: 400;
    color: #1e1e1e;
    margin-bottom: 14px;
    display: block;
    @media (max-width: 480px) {
      margin-bottom: 4px;
    }
  }
  &__price {
    font-size: 1.688rem;
    font-family: 'Century', sans-serif;
    margin-bottom: 14px;
    display: block;
    @media (max-width: 480px) {
      margin-bottom: 8px;
    }
  }
  &__accent {
    font-size: 1.25rem;
    color: #fd4141;
  }
}

.cloth {
  &__wrap {
    display: flex;
    @media (max-width: 768px) {
      display: block;
    }
  }
  &__title {
    font-size: 1.688rem;
    font-weight: 400;
    font-family: 'Century', sans-serif;
    margin-bottom: 30px;
  }
  &__logo {
    border: none;
  }
}

.cloth-left {
  width: 31.42%;
  margin-right: 2.5%;
  display: block;
  @media (max-width: 768px) {
    width: 100%;
    margin-right: 0;
    margin-bottom: 30px;
  }
  &__item {
    background-color: #222222;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 529px;
    padding: 0 20px;
    background-repeat: no-repeat;
    background-size: cover;
    @media (max-width: 768px) {
      height: 252px;
    }
    @media (max-width: 480px) {
      height: 180px;
    }
  }
}
.cloth-right {
  display: block;
  width: 65.75%;
  @media (max-width: 768px) {
    width: 100%;
  }
  &__wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__item {
    background-color: #222222;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 26px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 49%;
    padding: 0 40px;
    height: 252px;
    @media (max-width: 768px) {
      margin-bottom: 10px;
    }
    @media (max-width: 480px) {
      height: 180px;
      padding: 0 30px;
    }
    @media (max-width: 380px) {
      padding: 0 20px;
    }
    .cloth__logo {
      @media (max-width: 1024px) {
        width: 100%;
      }
    }
  }
}

.material-head {
  height: 203px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #181818;
  padding: 20px 30px;
}

.material {
  &_list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    @media (max-width: 768px) {
      justify-content: space-between;
    }
  }
  &_item {
    width: 22.92%;
    height: 293px;
    background-color: #181818;
    position: relative;
    margin-bottom: 24px;
    margin-right: 2.5%;
    @media (max-width: 768px) {
      margin-right: 0;
      width: 49%;
    }
    @media (max-width: 480px) {
      height: auto;
      margin-bottom: 8px;
    }
    &:nth-child(4n) {
      margin-right: 0;
    }
    &:hover {
      .material_text-wrap {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  &_img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &_text-wrap {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    transition: all 0.2s ease-in-out;
    background-color: rgba(0,0,0, 0.3);
    @media (max-width: 480px) {
      padding: 15px;
    }
  }
  &_name {
    font-size: 1.125rem;
    display: block;
    margin-bottom: 10px;
    @media (max-width: 480px) {
      margin-bottom: 3px;
    }
  }
  &_cost {
    font-size: 1.5rem;
    font-family: 'Century', sans-serif;
    display: block;
    @media (max-width: 480px) {
      font-size: 1.25rem;
    }
  }
}

.contacts-page  {
  display: flex;
  background-color: #f5f5f4;
  @media (max-width: 1024px) {
    display: block;
  }
  &__text {
    width: 50%;
    @media (max-width: 1024px) {
      width: 100%;
    }
  }
  &__text-wrap {
    padding: 65px;
    @media (max-width: 768px) {
      padding-left: 10px;
      padding-bottom: 20px;
    }
  }
  &__map {
    width: 50%;
    overflow: hidden;
    @media (max-width: 1024px) {
      width: 100%;
      height: 400px;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__title {
    color: #333439;
    font-family: 'Century', sans-serif;
    font-size: 2.25rem;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 55px;
    @media (max-width: 768px) {
      margin-bottom: 40px;
    }
    @media (max-width: 480px) {
      margin-bottom: 30px;
    }
  }
  &__block {
    font-size: 24px;
    margin-bottom: 50px;
    @media (max-width: 768px) {
      margin-bottom: 30px;
    }
    @media (max-width: 480px) {
      font-size: 18px;
    }
    span {
      display: block;
      margin-bottom: 10px;
    }
  }
  &__phone {
    display: block;
    margin-bottom: 10px;
    color: #333439;
    text-decoration: none;
  }
}
