.header {
  background-color: #9a9ea7;
  color: #ffffff;
  font-weight: 300;
  border-bottom: 1px solid $border;
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 115px;
    @media (max-width: 768px) {
      height: 90px;
    }
    @media (max-width: 480px) {
      height: 50px;
    }
  }
  &__right, &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    @media (max-width: 480px) {
      display: none;
    }
  }
  &__left {
    @media (max-width: 480px) {
      justify-content: space-between;
      width: 100%;
    }
  }
  &__column {
    width: 135px;
    margin-right: 30px;
    &:last-child {
      margin-right: 0;
      text-align: right;
    }
    span, a {
      margin-bottom: 12px;
      display: block;
      font-size: 0.875rem;
      &:last-child {
        margin-bottom: 0;
      }
      @media (max-width: 768px) {
        margin-bottom: 5px;
      }
    }
    @media (max-width: 1200px) {
      margin-right: 20px;
    }
    @media (max-width: 768px) {
      width: 125px;
    }
  }
  .social {
    @media (max-width: 768px) {
      display: none;
    }
  }
}
.header-phone {
  color: #ffffff;
  font-size: 1.125rem;
  text-decoration: none;
}

.nav-icon {
	margin-left: 15px;
  cursor: pointer;
  display: none;
  @media (max-width: 1024px) {
    display: block;
    z-index: 200;
    position: relative;
  }
	&__line {
		height: 2px;
  	width: 22px;
  	background:  #fff;
  	margin: 5px auto;
  	transition: all 300ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  	display: block;
	}
  &.active {
		 .nav-icon__line:nth-child(1) {
		    transform: rotate(45deg) translate(2px, 5px);
		  }
		  .nav-icon__line:nth-child(2) {
		     visibility: hidden;
		     transition: all 100ms
		  }
		  .nav-icon__line:nth-child(3) {
		     transform: rotate(-45deg) translate(5px, -8px);
	  }
  }
}

.nav {
  transition: all 0.3s ease;
  @media (max-width: 1024px) {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: $fontsLight;
    padding: 60px 25px;
    z-index: 100;
    visibility: hidden;
    opacity: 0;
  }
  &.active {
    visibility: visible;
    opacity: 1;
  }
  &__list {
    display: flex;
    @media (max-width: 1024px) {
      display: block;
      padding-top: 20px!important;
    }
  }
  &__item {
    margin-right: 40px;
    display: flex;
    align-items: center;
    @media (max-width: 1024px) {
      margin-right: 0;
      margin-bottom: 30px;
    }
    &:last-child {
      margin-right: 0;
    }
    &.dropdown {
      position: relative;
      @media (max-width: 1024px) {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      }
      &:after {
        content: '';
        width: 6px;
        height: 5px;
        background-image: url(../images/sprite.svg#arrow-down);
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        display: inline-block;
        margin-left: 10px;
        @media (max-width: 1024px) {
          position: absolute;
          right: 0;
          top: 3px;
        }
      }
      &:hover {
        .dropdown-menu {
          transition: all 0.5s ease;
          visibility: visible;
          opacity: 1;
          transform: translateX(0%);
          @media (max-width: 1024px) {
            position: relative;
            top: auto;
            left: auto;
            transform: translateX(0%);
          }
        }
      }
    }
  }
  &__link {
    color: #ffffff;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 0.875rem;
    position: relative;
    padding-bottom: 3px;
    overflow: hidden;
    @media (max-width: 1024px) {
      font-size: 1.125rem;
    }
    &:before {
      content: '';
      width: 100%;
      height: 1px;
      display: block;
      background-color: $accent;
      position: absolute;
      bottom: 0;
      left: 0;
      transform: translateX(-150%);
      transition: transform .2s ease;
    }
    &:hover:before {
      transform: translateX(0);
    }
  }
}

.dropdown-menu {
  background-color: $main-bg;
  position: absolute;
  top: 100%;
  left: 0;
  padding: 15px 20px 20px 20px;
  visibility: hidden;
  opacity: 0;
  transform: translateX(-100%);
  z-index: 20;
  &.active {
    transition: all 0.5s ease;
    visibility: visible;
    opacity: 1;
    transform: translateX(0%);
    @media (max-width: 1024px) {
      position: relative;
      top: auto;
      left: auto;
      transform: translateX(0%);
    }
  }
  @media (max-width: 1024px) {
    background: none;
    padding: 10px 10px 0 10px;
    width: 100%;
    transform: translateX(-100%);
  }
  &__item {
    text-decoration: none;
    color: $fonts;
    font-size: 0.875rem;
    margin-bottom: 8px;
    display: block;
    &:hover {
      color: $accent;
    }
    &:last-child {
      margin-bottom: 0;
    }
    @media (max-width: 1024px) {
      display: block;
      color: #fff;
      margin-bottom: 5px;
      font-size: 1rem;
    }
  }
}

.h-logo {
  margin-right: 46px;
  @media (max-width: 1024px) {
    margin-left: 30px;
  }
  @media (max-width: 480px) {
    width: 30px;
    margin-right: 0;
    margin-left: auto;
  }
}
