input {
  border: 1px solid #b7b7b7;
  padding: 17px 26px;
  background-color: transparent;
  font-size: 14px;
  width: 100%;
  min-width: 100%;
}


.form {
  width: 588px;
  @media (max-width: 768px) {
    width: 100%;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__description {
    color: #26282e;
    font-size: 12px;
    margin-bottom: 17px;
    span {
      color: $accent;
    }
  }
}



.form__group {
  margin-bottom: 20px;
  width: 100%;
  label {
    display: block;
    color: #26282e;
    font-size: 12px;
    margin-bottom: 8px;
    min-height: 15px;
    @media (max-width: 520px) {
      min-height: 0;
    }
    span {
      color: $accent;
    }
  }
  &.w50 {
    width: 49%;
    @media (max-width: 520px) {
      width: 100%;
    }
  }
}
