@import 'fonts.scss';
@import 'var.scss';
@import 'general.scss';
@import 'header.scss';
@import 'form.scss';
@import 'footer.scss';



.main	{
	background-color: #9a9ea7;
	color: #ffffff;
	background-image: url(../images/main.png);
	background-repeat: no-repeat;
	background-position: right 200px bottom;
	@media (max-width: 1700px) {
		background-position: right bottom;
	}
	@media (max-width: 1100px) {
		background-image: none;
	}
	@media (max-width: 768px) {
		background-position: center bottom;
		background-image: url(../images/main_768.png);
	}
	@media (max-width: 480px) {
		background-image: url(../images/main_480.png);
	}
	&__wrap {
		min-height: 660px;
		padding-top: 98px;
		@media (max-width: 768px) {
			padding-top: 50px;
			padding-bottom: 430px;
			text-align: center;
			display: flex;
			flex-direction: column;
			align-items: center;
		}
		@media (max-width: 480px) {
			padding-bottom: 240px;
			min-height: auto;
			padding-top: 30px;
		}
	}
	&__logo {
		margin-bottom: 48px;
		@media (max-width: 768px) {
			margin-bottom: 20px;
		}
		@media (max-width: 480px) {
			width: 80%;
		}
	}
	&__title {
		font-size: 3rem;
		font-weight: 400;
		font-family: 'Century', sans-serif;
		text-transform: uppercase;
		max-width: 553px;
		display: block;
		width: 100%;
		color: #fff;
		@media (max-width: 480px) {
			font-size: 2rem;
		}
	}
}

.founder {
	background-image: url(../images/clasp_1.png), url(../images/clasp_2.png), url(../images/clasp_3.png);
	background-repeat: no-repeat;
	background-position: left 100px center, top 80px right 90px, right 100px bottom 50px;
	@media (max-width: 1600px) {
		background-position: left center, top 20px right, right bottom;
	}
	@media (max-width: 1024px) {
		background: none;
	}
	&__wrap {
		display: flex;
		@media (max-width: 768px) {
			display: block;
		}
	}
	&__text-wrap {
		padding: 78px 30px 30px 30px;
		width: 50%;
		@media (max-width: 768px) {
			width: 100%;
		}
		@media (max-width: 480px) {
			padding: 30px 0px;
		}
	}
	.photo {
		width: 50%;
		@media (max-width: 768px) {
			width: 100%;
		}
	}
}

.block-text {
	padding-left: 30px;
	color: $fontsLight;
	font-size: 1.125rem;
	font-weight: 400;
	line-height: 1.75rem;
	@media (max-width: 480px) {
		padding-left: 15px;
	}
}

.photo {
	padding: 41px 63px;
	background-image: url(../images/photo_bg.png), url(../images/photo_bg-2.png);
	background-position: top left, bottom right;
	background-repeat: no-repeat;
	@media (max-width: 480px) {
		padding: 20px 24px;
		background-image: url(../images/photo_bg_480.png), url(../images/photo_bg-2_480.png);
	}
	&--reverse {
		background-position: top right, bottom left;
	}
}

.about {
	&__wrap {
		display: flex;
		@media (max-width: 768px) {
			display: block;
		}
	}
	&__left, &__right {
		width: 50%;
		@media (max-width: 768px) {
			width: 100%;
		}
	}
	&__left {
		padding: 48px 80px 30px 30px;
		@media (max-width: 480px) {
			padding: 30px 0px;
		}
	}
	&__right {
		padding: 50px 0px 180px 100px;
		background-image: url(../images/clasp_1.png), url(../images/clasp_2.png), url(../images/clasp_3.png);
		background-repeat: no-repeat;
		background-position: right bottom, bottom 100px left, left 80px top 30px;
	}
}


.advantages {
	position: relative;
	background-color: #9a9ea7;
	&__bg {
		background-image: url(../images/advantages.jpg);
		background-size: cover;
		width: 100%;
		height: 100%;
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0.17;
	}
	.title {
		color: #fff;
	}
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-top: 80px;
		@media (max-width: 480px) {
			margin-top: 40px;
		}
	}
	&__item {
		width: 33%;
		color: #fff;
		font-size: 1.125rem;
		padding: 160px 60px 70px;
		position: relative;
		text-align: center;
		@media (max-width: 768px) {
			width: 50%;
		}
		@media (max-width: 480px) {
			padding: 126px 20px 40px;
		}
		@media (max-width: 340px) {
			padding: 126px 10px 40px;
		}
	}
	&_icon {
		position: absolute;
		top: 20px;
		left: 0;
		right: 0;
		margin: auto;
		@media (max-width: 480px) {
			max-width: 80px;
		}
	}
}

.works {
	padding-bottom: 220px;
	background-image: url(../images/clasp_3.png);
	background-repeat: no-repeat;
	background-position: left 360px bottom;
	@media (max-width: 1024px) {
		padding-bottom: 0;
		background-position: right 60px bottom;
	}
	@media (max-width: 480px) {
		padding-bottom: 20px;
		background: none;
	}
	&__list {
		display: flex;
		@media (max-width: 1024px) {
			flex-wrap: wrap;
		}
		@media (max-width: 768px) {
			justify-content: space-between;
		}
	}
	&__item {
		margin-right: 1.125rem;
		display: block;
		width: 20%;
		&:last-child {
			margin-right: 0;
		}
		@media (max-width: 1024px) {
			width: 32%;
			margin-bottom: 10px;
			margin-right: 10px;
		}
		@media (max-width: 768px) {
			width: 49%;
			margin-right: 0;
		}
		@media (max-width: 480px) {
			width: 100%;
			height: 250px;
			overflow: hidden;
		}
	}
	&__img {
		border: none;
		width: 100%;
		object-fit: cover;
	}
}

.process {
	&__list {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	&__item {
		width: 33%;
		@media (max-width: 768px) {
			width: 100%;
			margin-bottom: 40px;
		}
		@media (max-width: 480px) {
			margin-bottom: 20px;
		}
	}
	&__title {
		font-size: 1.125rem;
		font-weight: 400;
		padding-right: 20px;
	}
}
.process-img {
	position: relative;
	margin-bottom: 46px;
	@media (max-width: 768px) {
		height: 300px;
		overflow: hidden;
		margin-bottom: 30px;
	}
	@media (max-width: 480px) {
		margin-bottom: 15px;
	}
	&__title {
		position: absolute;
		bottom: 39px;
		right: 43px;
		color: #ffffff;
		font-size: 2.25rem;
		font-family: Century;
		text-transform: uppercase;
		@media (max-width: 1024px) {
			font-size: 1.25rem;
			bottom: 10px;
			right: 10px;
		}
		span {
			display: block;
			font-size: 100px;
			line-height: 83px;
			@media (max-width: 1024px) {
				font-size: 3.25rem;
				line-height: 40px;
			}
		}
	}
	img {
		width: 100%;
	}
}

.comments {
	background-color: #9a9ea7;
	.title {
		color: #fff;
	}
}

.comments-block {
	display: flex;
	@media (max-width: 480px) {
		display: block;
	}
	&__text {
		padding: 40px 50px;
		@media (max-width: 1200px) {
			padding: 20px 20px 20px 10px;
		}
	}
	&__img {
		background-image: url(../images/photo_bg-3.png);
		padding: 24px 20px;
		background-position: left bottom;
		background-repeat: no-repeat;
		max-height: 294px;
		img {
			width: 247px;
			min-width: 247px;
		}
	}
	&__title {
		color: #ffffff;
		font-family: Century;
		font-size: 24px;
		text-transform: uppercase;
		font-weight: 400;
		margin-bottom: 33px;
	}
	&__description {
		color: #ebebeb;
		font-size: 16px;
	}
}

.slider-arrow {
	display: flex;
	justify-content: space-between;
	margin-top: 50px;
}

.assortment {
	padding-left: 20px;
	padding-right: 20px;
	@media (max-width: 480px) {
		padding-left: 10px;
		padding-right: 10px;
	}
	&__list {
		display: grid;
		grid-template-rows: 310px 310px 310px;
		grid-template-columns: repeat(5, 1fr);
		grid-gap: 1vw;
		grid-template-areas:
		'a1 a1 a2 a3 a3'
		'a4 a5 a6 a3 a3'
		'a7 a5 a8 a9 a10';
		@media (max-width: 1024px) {
			grid-template-rows: repeat(4, 310px);
			grid-template-columns: 1fr 1fr 1fr;
			grid-template-areas:
			'a3 a3 a3'
			'a1 a2 a4'
			'a5 a6 a7'
			'a8 a9 a10';
		}
		@media (max-width: 768px) {
			grid-template-rows: repeat(6, 310px);
			grid-template-columns: 1fr 1fr;
			grid-template-areas:
			'a3 a3'
			'a1 a2'
			'a4 a4'
			'a5 a6'
			'a7 a8'
			'a9 a10';
		}
		@media (max-width: 480px) {
			grid-template-rows: repeat(10, 260px);
			grid-template-columns: 1fr;
			grid-template-areas:
			'a1'
			'a2'
			'a3'
			'a4'
			'a5'
			'a6'
			'a7'
			'a8'
			'a9'
			'a10';
		}
	}
	&__item {
		position: relative;
		background-color: #1a140d;
		&.a1 {
			grid-area: a1;
		}
		&.a2 {
			grid-area: a2;
		}
		&.a3 {
			grid-area: a3;
		}
		&.a4 {
			grid-area: a4;
		}
		&.a5 {
			grid-area: a5;
		}
		&.a6 {
			grid-area: a6;
		}
		&.a7 {
			grid-area: a7;
		}
		&.a8 {
			grid-area: a8;
		}
		&.a9 {
			grid-area: a9;
		}
		&.a10 {
			grid-area: a10;
		}
		&:hover {
			.assortment__img {
				opacity: 1;
			}
			.assortment__title {
				border-left: 8px solid $accent;
				&:before {
					transform: translateX(0%);
					transition: all 0.4s ease;
				}
			}
		}
		&.big {
			border: 10px solid $accent;
			background-color: #232530;
			@media (max-width: 1500px) {
				border-width: 6px;
			}
			@media (max-width: 768px) {
				border-width: 5px;
			}
			.assortment__img {
				opacity: 0.3;
			}
		}
	}
	&__img {
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0.6;
		transition: all 0.5s ease;
	}
	&__title {
		color: #ffffff;
		text-decoration: none;
		font-family: 'Century', sans-serif;
		font-size: 1.5rem;
		font-weight: 400;
		text-transform: uppercase;
		z-index: 20;
		position: absolute;
		bottom: 0;
		border-top: 1px solid #ebebeb;
		width: 100%;
		border-left: 8px solid transparent;
		margin-bottom: 0;
		height: 95px;
		display: flex;
		align-items: center;
		padding: 3px 3px 3px 40px;
		overflow: hidden;
		&:before {
			content: '';
			width: 100%;
			height: 100%;
			background-color: #0c0f17;
			position: absolute;
			top: 0;
			left: 0;
			transform: translateX(-100%);
			z-index: -1;
		}
		@media (max-width: 1500px) {
			padding-left: 10px;
			font-size: 1rem;
		}
		@media (max-width: 768px) {
			font-size: 1.25rem;
			padding-left: 20px;
		}
		@media (max-width: 480px) {
			padding-left: 10px;
		}
	}
	&__title-2 {
		color: #ffffff;
		font-family: 'Century', sans-serif;
		font-size: 2.25rem;
		font-weight: 400;
		text-transform: uppercase;
		margin-bottom: 90px;
		@media (max-width: 1500px) {
			font-size: 1.45rem;
			margin-bottom: 60px;
		}
		@media (max-width: 1024px) {
			margin-bottom: 34px;
		}
		@media (max-width: 768px) {
			font-size: 1.25rem;
			margin-bottom: 30px;
		}
	}
	&__text {
		z-index: 20;
		position: relative;
		color: #fff;
		padding: 60px;
		@media (max-width: 1500px) {
			padding: 50px 40px;
		}
		@media (max-width: 480px) {
			padding: 30px 20px;
		}
	}
}

.form-section {
	position: relative;
	overflow-x: hidden;
	@media (max-width: 1200px) {
		padding: 0 10px;
	}
	&__wrap {
		background-image: url(../images/form_bg.png);
		background-repeat: no-repeat;
		background-position: bottom right;
		padding: 60px 0;
		@media (max-width: 980px) {
			background: none;
		}
	}
	.subtitle {
		padding-left: 30px;
		margin-bottom: 50px;
		display: block;
		@media (max-width: 480px) {
			padding-left: 15px;
			margin-bottom: 30px;
		}
	}
}



@import 'inner.scss';
@import 'calculator.scss';
