* {
	outline: none;
	box-sizing: border-box;
}
*::before, *::after {
	box-sizing: border-box;
}
html {
	font-size: 16px;
	@media (max-width: 1024px) {
		font-size: 15px;
	}
	@media (max-width: 768px) {
		font-size: 14px;
	}
	@media (max-width: 480px) {
		font-size: 12px;
	}
}
body {
	background-color: $main-bg;
	font-family: 'Avenir Next Cyr', sans-serif;
	color: $fonts;
	margin: 0;
	min-height: 100vh;
	scroll-behavior: smooth;
}
ul[class], ol[class] {
	list-style: none;
	padding: 0;
	margin: 0;
}
h1[class], h2[class], h3[class], h4[class], h5[class] {
	margin-top: 0;
}
h1 {
	font-size: 2.25rem;
	font-family: 'Century', sans-serif;
	color: #333439;
	font-weight: 400;
	@media (max-width: 1024px) {
		font-size: 2rem;
	}
}
.uppercase {
	text-transform: uppercase;
}
img {
	max-width: 100%;
	display: block;
}
input, button, textarea, select {
	font: inherit;
}

.container {
	// min-width: 100%;
	max-width: $container;
	width: 100%;
	margin: 0 auto;
}
.content-wrap {
  max-width: $content;
  margin: 0 auto;
	@media (max-width: 1200px) {
		padding: 0 10px;
	}
}

.link {
  font-size: 0.875rem;
  font-weight: 300;
  color: $accent;
  &:hover {
    text-decoration: none;
  }
}
.section {
	padding: 60px 0;
	@media (max-width: 480px) {
		padding: 30px 0px;
	}
}
.title {
	font-family: Century;
	text-transform: uppercase;
	font-size: 2.25rem;
	position: relative;
	padding-left: 30px;
	margin-top: 0;
	margin-bottom: 40px;
	@media (max-width: 480px) {
		margin-bottom: 20px;
		padding-left: 15px;
		font-size: 2rem;
	}
	&:before {
		content: '';
		width: 8px;
		height: 31px;
		display: block;
		background-color: $accent;
		position: absolute;
		left: 0;
		top: 8px;
		@media (max-width: 480px) {
			width: 4px;
			height: 25px;
			top: 6px;
		}
	}
	span {
		color: $accent;
	}
}

.title-wrap {
	display: flex;
	align-items: center;
	margin-bottom: 50px;
	.title {
		margin-bottom: 0;
		margin-right: 30px;
	}
	.link {
		border-left: 1px solid #1a140d;
		padding-left: 30px;
	}
}

.subtitle {
	font-size: 1.125rem;
	font-weight: 400;
}

.arrow {
	width: 49px;
	height: 49px;
	border: 1px solid #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transition: all 0.2s ease;
	&:hover {
		border-color: $accent;
	}
	&-prev:before {
		content: '';
		display: block;
		width: 16px;
		height: 11px;
		background-image: url(../images/sprite.svg#arrow-prev);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
	}
	&-next:before {
		content: '';
		display: block;
		width: 16px;
		height: 11px;
		background-image: url(../images/sprite.svg#arrow-next);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
	}
}


.btn {
	color: $accent;
	border: 1px solid $accent;
	width: 220px;
	height: 49px;
	background-color: transparent;
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	position: relative;
	transition: all 300ms ease;
	z-index: 5;
	text-decoration: none;
	&:before {
		content: '';
		position: absolute;
		width: 0%;
		height: 100%;
		background-color: $accent;
		top: 0;
		left: 50%;
		z-index: -1;
		transition: all 0ms ease;
	}
	&:hover {
		color: #fff;
		&:before {
			width: 100%;
			height: 100%;
			top: 0;
			left: 0%;
			transition: all 300ms ease;
		}
	}
	@media (max-width: 480px) {
		min-width: 170px;
	}
}

.list {
	&__item {
		color: #ebebeb;
		font-size: 1.25rem;
		text-transform: uppercase;
		position: relative;
		padding-left: 38px;
		margin-bottom: 80px;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			top: 3px;
			width: 4px;
			height: 16px;
			background-color: $accent;
		}
		@media (max-width: 1500px) {
			margin-bottom: 50px;
			font-size: 1rem;
		}
		@media (max-width: 1024px) {
			margin-bottom: 26px;
		}
		@media (max-width: 768px) {
			margin-bottom: 15px;
			font-size: 1rem;
		}
	}
}

.calc {
	position: fixed;
	right: 0;
	top: 150px;
	display: flex;
	height: 102px;
	transform: translateX(254px);
	transition: all 0.3s ease-in-out;
	@media (max-width: 768px) {
		position: relative;
		top: auto;
		transform: translateX(0px);
		margin-bottom: 60px;
	}
	&:hover {
		transform: translateX(0px);
	}
	&__left {
		box-shadow: 2px 3px 35px rgba(30, 31, 38, 0.06);
		border-radius: 17px 0 0 17px;
		background-color: #1e1f26;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 115px;
		@media (max-width: 768px) {
			border-radius: 0;
		}
		&:after {
			content: '';
			display: block;
			width: 54px;
			height: 60px;
			background-image: url(../images/sprite.svg#calc);
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}
	}
	&__right {
		background-color: #ffffff;
		width: 254px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 5px 10px;
		text-align: center;
		@media (max-width: 768px) {
			width: 100%;
		}
	}
	span {
		color: #232530;
		font-size: 16px;
		text-decoration: none;
		cursor: pointer;
		&:hover {
			color: $accent;
		}
	}
}

.inner-page {
	background-color: #fff;
}


.breadcrumb {
	padding: 10px;
	@media (max-width: 480px) {
		padding-left: 0;
	}
	&.background {
		background-color: #f5f5f4;
	}
	&__item {
		text-decoration: none;
		color: #6c6e75;
		font-size: 0.875rem;
		padding: 0px 20px;
		position: relative;
		@media (max-width: 480px) {
			padding: 0 12px;
		}
		&:after {
			content: '';
			display: block;
			width: 1px;
			height: 15px;
			background-color: #6c6e75;
			position: absolute;
			right: 0;
			top: 0px;
		}
		&.active {
			pointer-events: none;
			&:after {
				display: none;
			}
		}
	}
}

.no-scroll {
	overflow: hidden;
}
